#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: left;
}

.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  a:nth-of-type(2) .logo {
    animation: logo-spin infinite 20s linear;
  }
}

.card {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}

h2 {
  font-size: 23px;
  margin: 0;
  margin-bottom: 10px;
}

h3 {
  font-size: 18px;
  margin: 0;
  margin-bottom: 10px;
  font-style: italic;
}

.label-wrapper {
  width: 350px;
  display: grid;
}

.label-wrapper input {
  font-size: 15px;
  height: 25px;
  border-radius: 5px;
}

.label-wrapper label {
  font-style: italic;
  margin-left: 5px;
}